$osu: #d73f09;
$neutral-100: #f7f5f5;
$paddletailblack: #212529;
$bucktoothwhite: #ffffff;
$body: #423E3C;

// CKE-specific styles - Must be duplicated outside of .cke_reset_all to work outside of edit mode
.cke_reset_all {
  // Color choice boxes
  .osu-orange {
    color: #d73f09;
  }

  .osu-gray {
    color: #f7f5f5;
  }

  .osu-black {
    color: #212529;
  }

  .osu-body {
    color: $body;
  }

  .primary {
    background-color: $osu;
  }

  .light {
    background-color: $neutral-100;
  }

  .dark {
    background-color: $paddletailblack;
  }

  form.osu-buttons {
    label {
      font-weight: bold;
    }

    input, select {
      border: 1px solid #ced4da;
      padding: .375rem .75rem;
    }
  }

  // Button Styles
  .btn {
    background-color: $neutral-100;
    border: 1px solid $neutral-100;
    border-radius: 50rem;
    color: $body;
    cursor: pointer;
    display: inline-block;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    padding: 0.375rem 3rem;
    text-align: center;
    text-decoration: none;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    user-select: none;
    vertical-align: middle;

    &:hover, &:active, &:focus {
      background-color: $neutral-100;
      text-decoration: underline;
    }

    &.osu-btn-primary {
      background-color: $osu;
      color: $bucktoothwhite;

      &:hover, &:active, &:focus {
        background-color: $osu;
        color: $bucktoothwhite;
      }
    }

    &.osu-btn-light {
      background-color: $neutral-100;
      color: $body;

      &:hover, &:active, &:focus {
        background-color: $osu;
        color: $bucktoothwhite;
      }
    }

    &.osu-btn-dark {
      background-color: $paddletailblack;
      color: $bucktoothwhite;

      &:hover, &:active, &:focus {
        background-color: $osu;
        color: $bucktoothwhite;
      }
    }

  }

  .btn-sm {
    padding: 0.25rem 3rem;
    font-size: 0.875rem;
    border-radius: 50rem;
  }

  .btn-lg {
    padding: 0.5rem 3rem;
    font-size: 1.25rem;
    border-radius: 50rem;
  }

  #selected-button {
    float: right;
    position: relative;
  }

  .button-sizes a {
    cursor: pointer;
  }

  .button-sizes {
    a {
      &.size-active {
        background: #aaa;
        font-weight: bold;
      }
    }
  }

  .selected-size {
    border: 10px solid transparent;
  }
}

